.search-all {
	margin-top: 25px;
}

.search-input-areas {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	text-align: center;
	color: #222;
}

.search-input-areas > form {
	width: 100%;
}

.search-input {
	width: 50%;
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
}

.search-input-areas > form {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	text-align: center;
	font-size: 16px;
	color: #222;
}

.search-input {
	padding: 7px 20px;
	border-radius: 2px;
	outline: none;
	border: none;
	font-size: 18px;
	border: 1px solid #555;
}

@media screen and (max-width: 820px) {
	.search-input {
		width: 70%;
	}
}
