.register-subscription {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 24px;
	padding: 24px;
	color: #222;
}

.input-areas > form {
  display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 24px;
	padding: 24px;
  font-size: 16px;
	color: #222;
}

.input-areas > h3 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
}

.register-subscription > p {
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
}

.register-subscription-heading {
	margin-bottom: 24px;
	font-size: 24px;
}

.register-subscription-text {
	margin-bottom: 24px;
	font-size: 20px;
}

.register-input {
  padding: 7px 20px;
  border-radius: 6px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #555;
}

@media screen and (max-width: 820px) {
	.register-input {
		width: 100%;
	}
}
