.word-content {
	display: flex;
	flex-direction: row;
	width: 100%;
	max-width: 1140px;
	min-width: 460px;
	min-height: 200px;
	margin: 10px auto;
	overflow: hidden;
	outline: 1px solid #c9c9c9;
	border: 1px solid #fff;
	background-color: #e9e9e9;
}

.word-list,
.word-description {
	min-height: 200px;
}

.word-list {
	width: 25%;
	border-right: 0 solid #d9d9d9;
	padding: 10px 10px 20px;
	float: left;
}

.word-description {
	width: 75%;
	height: 100%;
	background-color: #fff;
	padding: 10px 20px 20px;
	float: left;
}

.word-description-heading {
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
	font-size: 25px;
}

.word-description-text {
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
	font-size: 16px;
}